import React, {useState} from 'react';
import { Panel, PanelHeader, Placeholder, Button, Div, ScreenSpinner, Header } from '@vkontakte/vkui/';
import bridge from '@vkontakte/vk-bridge';
import './Style.css';
import Icon56DiamondOutline from '@vkontakte/icons/dist/56/diamond_outline';
import icon from '../img/icon.png';

const Intro = props => {

    const [chooseTeam, setChooseTeam] = useState(false)

    return (
        <Panel id='Intro'>
            <Placeholder
                icon={<Icon56DiamondOutline fill="#00f2fe"/>}
                header='Red VS Blue'
                action={<>{chooseTeam === true ? (
                
                
                <Div style={{ display: 'flex' }}>
                    <Button 
                    onClick={() => {
                                bridge.send("VKWebAppStorageSet", { "key": "useHasSeenIntro", "value": 'true' });
                        props.hasSeenIntro('Home')
                        props.selectTeam('blue')
                        }
                    } size="l" className='customButtonInfo' stretched style={{ marginRight: 8 }}>&nbsp; Синие &nbsp;</Button>
                    <Button 
                    onClick={() => {
                                bridge.send("VKWebAppStorageSet", { "key": "useHasSeenIntro", "value": 'true' });
                        props.hasSeenIntro('Home')
                        props.selectTeam('red')
                        }
                    } size="l" className='customButtonInfo' stretched>Красные</Button>
                </Div>
                
                
                
                
                ) : (
                        <Div><Button size='xl' className='customButtonInfo'
                            onClick={() => {
                                props.setPopoutSpin(<ScreenSpinner />)
                                setTimeout(() => {
                                    props.setPopoutSpin(null)
                                    setChooseTeam(true)
                                }, 500);
                            }
                            }
                        >Поехали!</Button> 
                        </Div>
                    )}</>}
                stretched
            >
                {chooseTeam === true ? (<><br />Выберите цвет команды:</>) : (
                    <><br />Добро пожаловать в противостояние «Синих» и «Красных»! <br /><br /> Кликай, чтобы привести свою команду к Победе!<br /><br /> Победители определятся в конце дня!</>
                    )}
          </Placeholder>
          
            
           
        </Panel>

    )
};


export default Intro;
