import React, {useState, useEffect} from 'react';
import { Panel, PanelHeader, PanelHeaderBack, Button, Div, Group, Header, RichCell, List, Cell} from '@vkontakte/vkui/';
import './Style.css';

const Rating = props => {

	const [selectTeam, setSelectTeam] = useState(true)


	const selectTeamRating = (team) => {
		if (team === 'blue') {
			setSelectTeam(true)
		}
		if (team === 'red') {
			setSelectTeam(false)
		}

	}

	useEffect(() => {
	
		console.log(props.yourStat);
		console.log(props.blueTeam);
		console.log(props.redTeam);	
		
		

	}, []);


	return (
		<Panel id='Rating'>
			<PanelHeader transparent left={<PanelHeaderBack onClick={() => window.history.back()} />}>
				<span className='customText'>Рейтинг</span>
			</PanelHeader>
			<Group
				header={<Header mode="secondary"></Header>}
			>
				<Div style={{ display: 'flex', marginTop: -30 }}>
					{selectTeam === true ? (<>
						<Button disabled onClick={() => selectTeamRating('blue')} className='customText' size="l" stretched mode="tertiary" style={{ marginRight: 8 }}>Cиних — {props.blueTeam.length}</Button>
						<Button onClick={() => selectTeamRating('red')} className='customText' size="l" stretched mode="tertiary">Красных — {props.redTeam.length}</Button>
					</>) : (<>
							<Button onClick={() => selectTeamRating('blue')} className='customText' size="l" stretched mode="tertiary" style={{ marginRight: 8 }}>Cиних — {props.blueTeam.length}</Button>
							<Button disabled onClick={() => selectTeamRating('red')} className='customText' size="l" stretched mode="tertiary">Красных — {props.redTeam.length}</Button>
					</>)}

					

				</Div>
				
				

				{selectTeam === true ? ( <>
					
					
					{props.blueTeam.length === 0 ? (<>
							<List>
								<Cell className='customText' style={{textAlign: 'center'}} multiline>Нету данных для отображения.</Cell>
							</List>
						</>) : (props.blueTeam.map(user => {
							return <>
								<RichCell
									className='customText'
									after={user[1]}
									onClick={() => window.open(user[0].link, "_blank")}
								>
									{user[0].firstname} {user[0].lastname}
								</RichCell>
							</>
						}))
					}
						
				</>) : (<>


					{props.redTeam.length === 0 ? (<>
						<List>
								<Cell className='customText' style={{ textAlign: 'center' }} multiline>Нету данных для отображения.</Cell>
						</List>
						</>) : (props.redTeam.map(user => {
						return <>
							<RichCell
								className='customText'
								after={user[1]}
								onClick={() => window.open(user[0].link, "_blank")}
							>
								{user[0].firstname} {user[0].lastname}
							</RichCell>
						</>
					}))
					}

				</>)}
				{props.yourStat != '' ? (<>
					<List>
						<Cell className='customText' style={{ textAlign: 'center' }} multiline>Ваш рейтинг — {props.yourStat}</Cell>
					</List> </>) : (<></>)}

			</Group>
		</Panel>

	)
};


export default Rating;
