import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner } from '@vkontakte/vkui/';
import ConfigProvider from '@vkontakte/vkui/dist/components/ConfigProvider/ConfigProvider';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home'
import Info from './panels/Info'
import Intro from './panels/Intro'
import Loader from './panels/Loader'
import Rating from './panels/Rating'
import Finish from './panels/Finish'

import socketIO from 'socket.io-client';

const socket = socketIO("https://83.220.173.46/message", {
	transports: ['websocket'], jsonp: false,
	methods: ["GET", "POST"],
	credentials: true
});

const App = () => {

	const [redScore, setRedScore] = useState(0)
	const [blueScore, setBlueScore] = useState(0)
	const [blueTeam, setBlueTeam] = useState()
	const [redTeam, setRedTeam] = useState()
	const [yourStat, setYourStat] = useState(0)

	const selectTeam = (color) => {
		socket.emit('color', { "color": color });
		socket.emit('score', { "score": "all" });
	}

	const clickButton = () => {
		socket.emit('statistic', { "statistic": "all" });
		socket.emit('click', { "click": "coordenate" });
		socket.emit('score', { "score": "all" });
	}

	const statistic = () => {
		socket.emit('statistic', { "statistic": "all" });
		socket.emit('score', { "score": "all" });

 }
 const undColor = () => {
	 setActivePanel('Finish')
 }
	
	useEffect(() => {
		statistic()

		bridge.send("VKWebAppGetUserInfo")
			.then(data => {

				socket.on("connect", (socketd) => {

					if (socketd) {

						

						const qs = require('querystring');
						const urlParams = qs.parse(window.location.search);
						const ordered = {};
						Object.keys(urlParams).sort().forEach((key) => {
							if (key.slice(0, 3) === 'vk_') {
								ordered[key] = urlParams[key];
							}
						});
						const userData = {
							'validate': ordered,
							'user_profile': {
								'id': data.id,
								'lastname': data.last_name,
								'firstname': data.first_name,
								'link': `https://vk.com/id${data.id}`
							}
						}

						if (socketd.connect === 'connect') {
							socket.emit('statistic', { "statistic": "all" });
							socket.emit('score', { "score": "all" });
							socket.emit("enter", userData);
						}

					}

				});
				socket.on('disconnect', (socketd) => {
					if (socketd) {
						if (socketd.connect === "disconnect") {
						}
					}})
					


			})
		
		socket.on("statistic", (socketd) => { console.log(socketd); 
			if (socketd.you_statistic != null) { setYourStat(socketd.you_statistic[1]) };
			if (socketd.statistic.red != null) { setRedTeam(socketd.statistic.red) };
			if (socketd.statistic.blue != null) { setBlueTeam(socketd.statistic.blue) };	
		  });
		socket.on("color", (socketd) => { });
		
		socket.on("score", (socketd) => { 
			
			if (socketd.score === 'restart') { 
			bridge.send("VKWebAppStorageGet", { "keys": ['useHasSeenIntro'] })
				.then(data => {
					if (data.keys[0].value === 'true') {
						goToPage('Finish') 
					}
				})	
		}; 

		socket.emit('statistic', { "statistic": "all" }); if (socketd.red) { setRedScore(socketd.red) }; if (socketd.blue) { setBlueScore(socketd.blue) } });
		socket.on("disconnect", () => {

		});

		socket.on("click", (socketd) => {

			if (socketd) {
				if (socketd.click === "select") {
					bridge.send("VKWebAppStorageGet", { "keys": ['useHasSeenIntro'] })
						.then(data => {
							if (data.keys[0].value === 'true') {
								goToPage('Finish')
							}
						})	
				}
				else {
					socket.emit('score', { "score": "all" });
					if (socketd.click === "success") {
						socket.emit('score', { "score": "all" })
					}
					if (socketd.click === "error") {
					}
					if (socketd.click === "flood") {
					}
				}
			}

			
		});


		



		window.addEventListener('popstate', () => goBack());

	}, []);

	const [scheme, SetStateScheme] = useState('space_gray');
	const [popoutSpin, setPopoutSpin] = useState(<ScreenSpinner />)
	const [activePanel, setActivePanel] = useState("Loader");
	const [history, setHistory] = useState(['Home'])

	const hasSeenIntro = (panel) => {
		setActivePanel(panel)
	}

	const goToPage = (name) => {
		window.history.pushState({ panel: name }, name);
		setActivePanel(name);
		history.push(name);
	};

	const goBack = () => {
		if (history.length === 1) {
			bridge.send("VKWebAppClose", { "status": "success" });
		} else if (history.length > 1) {
			history.pop()
			setActivePanel(history[history.length - 1])
		}
	}




	return (
		<ConfigProvider isWebView={true} scheme={scheme}>
			<View popout={popoutSpin} activePanel={activePanel} history={history} onSwipeBack={goBack}>
				<Loader id="Loader" hasSeenIntro={hasSeenIntro} setPopoutSpin={setPopoutSpin} goToPage={goToPage} goBack={goBack} />
				<Intro id="Intro" setPopoutSpin={setPopoutSpin} selectTeam={selectTeam} hasSeenIntro={hasSeenIntro} goToPage={goToPage} goBack={goBack} />
				<Home id="Home"  blueScore={blueScore} redScore={redScore} goToPage={goToPage} clickButton={clickButton} />
				<Rating id="Rating" yourStat={yourStat} redTeam={redTeam} blueTeam={blueTeam} statistic={statistic} goToPage={goToPage} goBack={goBack} />
				<Info id="Info" selectTeam={selectTeam} goToPage={goToPage} goBack={goBack} />
				<Finish id="Finish" setPopoutSpin={setPopoutSpin} selectTeam={selectTeam} hasSeenIntro={hasSeenIntro} goToPage={goToPage} goBack={goBack} />
			</View>
		</ConfigProvider>
	);
}

export default App;

