import React, { useState, useRef, useEffect } from 'react';
import { Panel, PanelHeader, Div, Button, Header, Group, SimpleCell, Switch } from '@vkontakte/vkui/';
import Icon28StoryQuestionCircleFillViolet from '@vkontakte/icons/dist/28/story_question_circle_fill_violet';
import Icon28PollCircleFillGreen from '@vkontakte/icons/dist/28/poll_circle_fill_green';
import Icon28StoryFillCircleRed from '@vkontakte/icons/dist/28/story_fill_circle_red';
import Icon28UsersCircleFillYellow from '@vkontakte/icons/dist/28/users_circle_fill_yellow';
import globalCounterIcon from '../img/VS.png';
import clicking from '../img/clicking.png';
import icon from '../img/icon.png';
import './Style.css';
import bridge from '@vkontakte/vk-bridge';


const Home = props => {

	
	const canvas = useRef(null);
	const [web, setWeb] = useState(false)

	useEffect(() => {

		bridge.send("VKWebAppGetClientVersion")
		.then(data => {
			if (data.platform === 'web') {
				setWeb(true)
			}
			else {
				setWeb(false)
			}
		
		})

		const context = canvas.current.getContext("2d");
		const image = new Image();
		image.src = globalCounterIcon;
		image.onload = () => {
			context.drawImage(image, 0, 0);
			context.font = "48px Source Code Pro";
			context.fillStyle = 'white'
			context.fillText(props.blueScore, 165 - props.blueScore.toString().length * 13, 170);
			context.fillText(props.redScore, 635 - props.redScore.toString().length * 13, 170);			
		};

	}, [props.redScore, props.blueScore]);
 

	return (
		<Panel id='Home'>

			<PanelHeader transparent>
				<img className='iconHeader' src={icon} alt=''></img>
			</PanelHeader>

			<Group header={<Header mode="secondary" ><div className='customText'>Счет</div></Header>}>
				<Div>
					<canvas className='globalCounter' ref={canvas} width={796} height={313} />
				</Div>
			</Group>

			<Group header={<Header mode="secondary" ><div className='customText'>Меню</div></Header>}>
				{web === false ? (<>
					
					<Div><Button onClick={() => {
						const qs = require('querystring');
						const urlParams = qs.parse(window.location.search);
						const ordered = {};
						Object.keys(urlParams).sort().forEach((key) => {
							if (key.slice(0, 3) === 'vk_') {
								ordered[key] = urlParams[key];
							}
						});
						bridge.send("VKWebAppShare", { "link": `https://vk.com/app${ordered.vk_app_id}` });

					}
					} className='customButtonInfo' before={<Icon28UsersCircleFillYellow />} size="xl" mode="outline">Поделиться c друзьями</Button></Div>
					<Div><Button onClick={() => props.goToPage('Rating')} className='customButtonInfo' before={<Icon28PollCircleFillGreen />} size="xl" mode="outline">Глобальный рейтинг</Button></Div>

				<Div><Button onClick={() => props.goToPage('Info')} className='customButtonInfo' before={<Icon28StoryQuestionCircleFillViolet />} size="xl" mode="outline">О приложении</Button></Div>
					<Div>
						<Button onClick={() => props.clickButton()} className='clickButtonMain' size='xl' mode='outline'>
							<img className='clicking' src={clicking} alt=''></img>
						</Button>
					</Div>

				</>) : (<>
						<Div>
							<Button onClick={() => props.clickButton()} className='clickButtonMainWeb' size='xl' mode='outline'>
								<img className='clickingWeb' src={clicking} alt=''></img>
							</Button>
						</Div>
						<Div><Button onClick={() => {
							const qs = require('querystring');
							const urlParams = qs.parse(window.location.search);
							const ordered = {};
							Object.keys(urlParams).sort().forEach((key) => {
								if (key.slice(0, 3) === 'vk_') {
									ordered[key] = urlParams[key];
								}
							});
							bridge.send("VKWebAppShare", { "link": `https://vk.com/app${ordered.vk_app_id}` });

						}
						} className='customButtonInfo' before={<Icon28UsersCircleFillYellow />} size="xl" mode="outline">Поделиться c друзьями</Button></Div>

						<Div><Button onClick={() => props.goToPage('Rating')} className='customButtonInfo' before={<Icon28PollCircleFillGreen />} size="xl" mode="outline">Глобальный рейтинг</Button></Div>
						<Div><Button onClick={() => props.goToPage('Info')} className='customButtonInfo' before={<Icon28StoryQuestionCircleFillViolet />} size="xl" mode="outline">О приложении</Button></Div>

						

				</>)}
				

			</Group>
		</Panel>

	)
};

export default Home;

