import React, { useState, useEffect } from 'react';
import { Panel, PanelHeader, PanelHeaderBack, Cell, Div, Group, Button, List, Header } from '@vkontakte/vkui/';
import Icon28MessagesCircleFillYellow from '@vkontakte/icons/dist/28/messages_circle_fill_yellow';
import Icon28LightbulbCircleFillYellow from '@vkontakte/icons/dist/28/lightbulb_circle_fill_yellow';
import Icon28UsersCircleFillYellow from '@vkontakte/icons/dist/28/users_circle_fill_yellow';
import Icon28FavoriteCircleFillYellow from '@vkontakte/icons/dist/28/favorite_circle_fill_yellow';
import bridge from '@vkontakte/vk-bridge';

import './Style.css';

const Info = props => {

    const [truth, setTruth] = useState(false)
    const [textChangeTeam, setTextChangeTeam] = useState()

    useEffect(() => {
        if (props.colorTeam === 'red') {
            setTextChangeTeam('Вступить за синих')
        }

        if (props.colorTeam === 'blue') {
            setTextChangeTeam('Вступить за красных')
        }

    }, []);


    const seeTruth = () => {
        if (truth === false) {
            setTruth(true)
        } else {
            setTruth(false)
        }
    }
    
    return (
        <Panel id='Info'>
            <PanelHeader transparent left={<PanelHeaderBack onClick={() => window.history.back()} />}>
                <span className='customText'>Информация</span>
            </PanelHeader>
            <Group
            >
                <Div><Button className='customButtonInfo' onClick={() => bridge.send("VKWebAppAddToFavorites")} before={<Icon28FavoriteCircleFillYellow />} size="xl" mode="outline">Добавить в избранное</Button></Div>

                <Div><Button className='customButtonInfo' onClick={() => window.open("https://vk.me/redvsbluegame", "_blank")} before={<Icon28MessagesCircleFillYellow />} size="xl" mode="outline">Сообщить об ошибке</Button></Div>
                <Div><Button className='customButtonInfo' onClick={() => window.open("https://vk.com/redvsbluegame", "_blank")} before={<Icon28UsersCircleFillYellow />} size="xl" mode="outline">Официальная группа</Button></Div>
                <Div><Button onClick={()=> seeTruth()} className='customButtonInfo' before={<Icon28LightbulbCircleFillYellow />} size="xl" mode='tertiary' >В чем смысл игры?</Button></Div>
              
                {truth === true ? (
                
                    <Group header={<Header className='customText'>«Разделяй и властвуй» <br /> © Филипп Македонский — греческий <br /> военный и политический деятель.</Header>}>
                        <List>
                            <Cell className='customText' multiline>Казалось бы, перед вами простое мини-приложение. Но в эту игру вложен намного больший смысл, чем просто «клацание» по кнопке. Вся суть данного приложения, это провести маленькую парралель с нынешним устройством мира. Мы не просто так разделили Вас на красных и синих, придумав вымышленный конфликт. Этим мы хотели показать, что на Земле нету белых/черных, мусульман/христиан, богатых/нищих. Все войны и конфликты возникают лишь в угоду «мировой верхушки», которая натравливает целые народы против друг-друга, ради своей выгоды (всего лишь разделив их по определенному признаку). А обычный люд не видя всего этого, слепо следует указаниям этих правителей и погибает.   </Cell>
                            <Cell className='customText' multiline>Надеемся, что этим маленьким примером, мы внесли ясность в Ваши умы.</Cell>
                        </List>
                    </Group>

                ) : (
                       <></>
                    )}
                    
                    
            </Group>

        </Panel>
    )
};


export default Info;
