import React, { useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel } from '@vkontakte/vkui/';
import './Style.css';

const Loader = props => {

    useEffect(() => {
        bridge.send("VKWebAppStorageGet", { "keys": ['useHasSeenIntro'] })
        .then( data => {
            if (data.keys[0].value === 'true') {
                props.hasSeenIntro('Home')
            }
            else {
                props.hasSeenIntro('Intro')
            }
            props.setPopoutSpin(null)
        })
    }, []);

    return (
        <Panel id='Loader'></Panel>
    )
};


export default Loader;
