import React, { useState } from 'react';
import { Panel, PanelHeader, Placeholder, Button, Div, ScreenSpinner, Header } from '@vkontakte/vkui/';
import bridge from '@vkontakte/vk-bridge';
import './Style.css';
import Icon56DiamondOutline from '@vkontakte/icons/dist/56/diamond_outline';
import icon from '../img/icon.png';

const Finish = props => {


    return (
        <Panel id='Finish'>
            <Placeholder
                icon={<Icon56DiamondOutline fill="#00f2fe" />}
                header='Red VS Blue'
                action={<Div style={{ display: 'flex' }}>
                    <Button
                        onClick={() => {
                            window.history.back()
                            props.selectTeam('blue')
                        }
                        } size="l" className='customButtonInfo' stretched style={{ marginRight: 8 }}>&nbsp; Синие &nbsp;</Button>
                    <Button
                        onClick={() => {
                            window.history.back()
                            props.selectTeam('red')
                        }
                        } size="l" className='customButtonInfo' stretched>Красные</Button>
                </Div>}
                stretched
            >
                Выберите цвет команды:
            </Placeholder>



        </Panel>

    )
};


export default Finish;
